* {
    box-sizing: border-box;
    margin: 0;
}

body {
    font-family: 'Montserrat', sans-serif;
}

.container {
    max-width: 1140px;
    width: 95%;
    margin: 0 auto;
    padding: 1rem 0;
}

*,:after,:before {
    box-sizing: border-box;
    margin: 0;
}

.blog-goBack {
    text-decoration: none;
    font-size: 1.4rem;
    color: #a9a9a9;
    font-weight: 500;
    margin-bottom: 2rem;
    display: block;
}

.blog-wrap {
    max-width: 700px;
    margin: 0 auto;
}

.blog-wrap header  {
    text-align: center;
}
.blog-title {
    display: flex;
    justify-content: center;
}
.blog-wrap img  {
    width: 100%;
}

.blog-desc {
    padding: 1rem;
    margin-top: 1.5rem;
    font-size: 1.8rem;
}

.blog-date {
    font-size: 1.3rem;
    color: #a9a9a9;
    font-weight: 500;
    display: flex;
    justify-content: center;
}

.blog-subCategory {
    display: flex;
    justify-content: center;
}

.blog-subCategory > div  {
    margin: 1rem;
}

.chip {
    font-size: 1.3rem;
    background: -webkit-linear-gradient(left, #6190e8, #a7bfe8);
    background: linear-gradient(to right, #6190e8, #a7bfe8);
    color: #fff;
    padding: 0.3rem 0.5rem;
    border-radius: 5px;
    width: -moz-fit-content;
    width: -webkit-fit-content;
    width: fit-content;
    text-transform: capitalize;
}



